.WorkList {
  display: grid;
  margin-top: 12px;
  justify-items: center;
  justify-content: center;
  width: 100%;
  grid-gap: 5px;
  overflow: hidden;
  transition: height 1s ease-in-out;
}
.WorkList .WorkList_item {
  width: 150px;
  display: grid;
  padding: 5px;
  background: #847C6E;
  color: #E5DCCB;
  grid-template-columns: 115px 25px;
  justify-self: center;
  align-items: center;
  font-size: 14px;
  letter-spacing: 1px;
  z-index: 8;
}
.WorkList .WorkList_item .deleteIcon {
  color: #E5DCCB;
}

