.ButtonModalList {
  width: calc(100% - 25px);
  font-weight: 500;
  height: 50px !important;
  font-size: 15px;
  font-family: "Playfair Display", serif;
  align-items: center;
  justify-items: center;
  justify-content: center;
  display: grid;
  background: rgba(68, 68, 68, 0.95);
  letter-spacing: 2.5px;
  color: #E5DCCB;
  align-self: end;
  position: absolute;

}